.navbar-light .navbar-brand:hover {
    background-color: white;
}


.navbar-light .navbar-brand {
    padding-top: 15px;
}

.navbar-first{
    align-items: center;
    padding: 10px 20px;
}

.nav-link {
    padding: 10px 20px;
}

.header-wraper{
    .header-panel{
        display: flex;
        justify-content: space-between;
    }
}

.navbar a{
    color: rgba(0,0,0,.6);
    padding: 6px 16px
}

.navbar a:hover{
    color: rgba(0,0,0,.6);
    text-decoration: none;
}

.nav-item{
  padding: 1px 16px !important;

  a:hover{
    color: rgba(0,0,0,.6);
    text-decoration: none;
    }
}

.dropdown a{
    color: rgba(0,0,0,.6);
    padding-left: 8px;
}

