.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.payback-disclaimer{
  font-size: 22px;
  color: black;
}



.container{
  display: flex;
  flex-wrap: wrap-reverse;
}
.images{
  width:"30%"
}

.container .mealContainer{
  display: flex;
  padding:8px;
  /* margin:5px; */
  background-color: #fff;
}

.container .mealContainer .caption {
  margin-top: 5px;
  font-size: 14px;
  color: #000;
}

